
import React, {useState}from 'react'


export default function TextEditer() {

    const [text, Settext] = useState("Enter your content");

    let uppercas= ()=>{

       const newtext=text.toUpperCase();

     Settext(newtext);
    }
    
    let lovercase= ()=>{

        const newtext=text.toLowerCase();
 
      Settext(newtext);
     }

     let Capitalize= ()=>{

      const newtext=text[0].toUpperCase() +
      text.slice(1);

    Settext(newtext);
   }

   let textcopy= ()=>{

    const newtext=text.navigator.clipboard.writeText();

  Settext(newtext);
 }
     

    let onchangehandle= (event)=>{

        Settext(event.target.value);
    }

      let color={
        color:"red",
        backgroundColor:"gray"
      }

  return (
<>

<div className="row container-fluid">
    <div className="col" >

    <div className="card">
  <div className="card-header bg-info">
    Testing
  </div>
  <div className="card-body">
    <blockquote className="blockquote mb-0">

     
    <textarea value={text} onChange={onchangehandle} className='form-control' name="test" />
    <p>{text.split(" ").length  }</p>
    <br/>
    <button  onClick={uppercas} className='btn btn-primary'>Upper case</button>
    
    <button style={{margin:"6px"}} onClick={lovercase} className='btn btn-info'>Lover case</button>

    <button style={{margin:"6px"}} onClick={Capitalize} className='btn btn-success'>Capitalize</button>
    <button style={{margin:"6px"}} onClick={textcopy} className='btn btn-info'>Text Copy</button>



    </blockquote>
  </div>
</div>


    </div>

  </div>

</>
  
  )
}
