import React from 'react'
import { BrowserRouter, Routes, Route } from "react-router-dom";
import logo from './webkul.png';
import nopageimg from './404.jpeg';
import Navbar from './component/Navbar'
import Student from './component/Student'
import AboutUs from './component/Aboutus'
import TextEditer from './component/TextEditer'
import News from './component/News';
import Home from './component/Home';
import Song from './component/Songs';
import NoPage from './component/NoPage';

function App() {

  if (navigator.geolocation) {
    navigator.geolocation.getCurrentPosition(success, error);
  } else {
    console.log("Geolocation not supported");
  }

  function success(position) {
    // const latitude = position.coords.latitude;
    // const longitude = position.coords.longitude;

    const latitude = position.coords.latitude;
    const longitude = position.coords.longitude;

    console.log(`Latitude: ${latitude}, Longitude: ${longitude}`);
  }

  function error() {
    console.log("Unable to retrieve your location");
  }

  return (


    <BrowserRouter>
      <Routes>
        <Route path="/" element={<Navbar logo={logo} />}>
          <Route index element={<Home />} />
          <Route path='home' element={<Home />} />
          <Route path="aboutus" element={<AboutUs />} />
          <Route path="textediter" element={<TextEditer />} />
          <Route path="news" element={<News />} />
          <Route path="student" element={<Student />} />
          <Route path="songs" element={<Song />} />
          <Route path="*" element={<NoPage img={nopageimg}/>} />
        </Route>
      </Routes>
    </BrowserRouter>

  );


}


export default App;
